import React from "react";
import { useQuery } from "react-query3";
import { useNavigate } from "react-router-dom";
import { Button, Card, PageHeader, Tree } from "antd";
import axios from "axios";
const PowerShellIcon = React.lazy(() => import("components/pages/PSIcon.js"));
const { DirectoryTree } = Tree;


export default function Configuration() {
  const { data: configuration } = useQuery<string[]>(
    "/configuration"
  );

  const callback = async (insiders) => {
    const url = (await axios.post(`/api/v1/vscode?insiders=${insiders}`)).data;
    window.location.href = url;
  }

  const navigate = useNavigate();
  const parentKey = "0-0";

  const treeData = [
    {
      title: "Root",
      key: parentKey,
      children: configuration?.map((config, index) => ({
        title: config,
        key: `${parentKey}-${index}`,
        icon: <PowerShellIcon style={{ marginRight: 16 }} />,
        isLeaf: true,
        style: {
          fontSize: 16,
          verticalAlign: 'center'
        }
      })),
    },
  ];

  const onSelect = (keys: React.Key[], info: any) => {
    if (info?.selectedNodes[0]?.title === 'Root') return
    navigate(info?.selectedNodes[0]?.title);
  };

  return (
    <React.Fragment>
      <Card bordered={false} bodyStyle={{ padding: 0 }}>
        <PageHeader
          ghost={true}
          title={"Configurations"}
          subTitle={"Files used to configure PowerShell Universal."}
          extra={
            <Button.Group>
              <Button onClick={() => callback(false)}>Edit with VS Code</Button>
              <Button onClick={() => callback(true)}>Edit with VS Code Insiders</Button>
            </Button.Group>
          } />
      </Card>
      <DirectoryTree
        defaultExpandAll
        onSelect={onSelect}
        treeData={treeData}
      />
    </React.Fragment>

  );
}
